  padding: 0;
  list-style: none;
  border: none;
  width: auto;
  position: absolute;
  z-index: 10;
  text-align: center;
  bottom: 15px;
  right: 15px;
}
.flex-control-nav li {
  float: left;
  display: block;
  margin: 0 3px;
  width: 10px;
  height: 10px;
}
.flex-control-nav li a {
  display: block;
  cursor: pointer;
  text-indent: -9999px;
  width: 10px;
  height: 10px;
  border: 1px solid #fff;
  border-radius: 50%;
  transition: all 0.3s ease-in-out;
}
.flex-control-nav li:hover a,
.flex-control-nav li a.flex-active {
  background-color: #fff;
}
/*
 * Shortcode: Flip Box
 * -----------------------------------------------
*/
.card {
  float: left;
  min-height: 300px;
  padding-bottom: 25%;
  position: relative;
  text-align: center;
  width: 100%;
}
.card:nth-child(1) {
  margin-left: -3px;
  margin-right: 1px;
}
.card:nth-child(1),
.card .card:nth-child(3) {
  margin-right: 1px;
}
.card .card__front,
.card .card__back {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  transition: transform 0.3s;
}
.card .card__back {
  transform: rotateY(-180deg);
}
.card .card__text {
  display: inline-block;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  line-height: 20px;
}
.card.effect__hover:hover .card__front {
  transform: rotateY(-180deg);
}
.card.effect__hover:hover .card__back {
  transform: rotateY(0);
}
.card.effect__click.flipped .card__front {
  transform: rotateY(-180deg);
}
.card.effect__click.flipped .card__back {
  transform: rotateY(0);
}
.card.effect__random.flipped .card__front {
  transform: rotateY(-180deg);
}
.card.effect__random.flipped .card__back {
  transform: rotateY(0);
}
/*
 * Responsive : Flip Box
 * -----------------------------------------------
*/
@media only screen and (max-width: 767px) {
  .card {
    margin-bottom: 30px;
    min-height: 489px;
  }
}
@media only screen and (min-width: 501px) and (max-width: 600px) {
  .card {
    margin-bottom: 30px;
    min-height: 400px;
  }
}
@media only screen and (min-width: 450px) and (max-width: 500px) {
  .card {
    margin-bottom: 30px;
    min-height: 350px;
  }
}
@media only screen and (min-width: 320px) and (max-width: 449px) {
  .card {
    margin-bottom: 30px;
    min-height: 300px;
  }
}
