.jw-modal {
    /* modal container fixed across whole screen */
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 1000;
}
.jw-modal .jw-modal-body {
    padding: 20px;
    background: #fff;
    max-width: 800px;
    margin: auto;
    transform: translate(0, 50%);
    border: 1px solid #ccc;
    background: #fff;
    overflow: auto;
    border-radius: 4px;
    outline: none;
}

.jw-modal-background {
    /* modal background fixed across whole screen */
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;

    background-color: #000;
    opacity: 0.75;
    z-index: 900;
}

body.jw-modal-open {
    /* body overflow is hidden to hide main scrollbar when modal window is open */
    overflow: hidden;
}
.comment-form {
    background-color: #9ecbd2;
    border-radius: 10px;
    margin-bottom: 40px;
    padding: 20px;
    position: relative;
    grid-row: 1;
    grid-column: 1/3;
}

.comment-form::before {
    background: url("/assets/images/illo-gears.svg") center no-repeat;
    content: '';
    height: 80px;
    position: absolute;
    right: 0;
    top: -80px;
    width: 154px;
}

.comment-form-actions > button {
    background-color: #fce87c;
    border: 0;
    border-radius: 10px;
    color: #6ba2aa;
    display: inline-block;
    font-weight: 700;
    line-height: 3;
    padding: 0 40px;
    text-transform: uppercase;
    transition: 0.2s ease-in-out;
}

.comment-form-actions > button:active,
.comment-form-actions > button:focus,
.comment-form-actions > button:hover {
    background-color: #fbe259;
}

.comment-form > label {
    color: #6ba2aa;
    display: block;
    margin-bottom: 10px;
    text-transform: uppercase;
}

.comment-form-fields > input,
.comment-form-fields > textarea {
    border: 0;
    border-radius: 10px;
    box-sizing: border-box;
    margin-bottom: 10px;
    padding: 10px;
    width: 100%;
    border: 1px solid;
    outline: none;
    resize: none;
}
